import React, { PureComponent } from 'react';
import InputAdornment from '@material-ui/core/InputAdornment';
import Form from './Form';
import FormGenerator from './FormGenerator';
import { PROPERTY } from '../../graphql/query/remote';
import { CREATE_PROPERTY, UPDATE_PROPERTY } from '../../graphql/mutation/remote';
import { getDate } from '../../utils/date';

export const defaults = {
    name: {
        type: "text",
        default: "",
    },
    location: {
        type: "text",
        default: "",
    },
    price: {
        type: "number",
        default: 0,
        startAdornment: <InputAdornment position="start">₹</InputAdornment>
    },
    buyer: {
        type: "text",
        default: "",
        required: true,
        validate: val => {
            return val.length < 1 ? "required" : null
        }
    },
    buyerNumber: {
        type: "number",
        default: "",
        required: true,
        startAdornment: <InputAdornment position="start">+91</InputAdornment>,
        validate: val => {
            return val.length < 10 ? "Invalid Phone Number" : null
        }
    },
    totalReceivedAmount: {
        type: "number",
        default: 0,
        startAdornment: <InputAdornment position="start">₹</InputAdornment>
    },
    nextDueDate: {
        type: "date",
        default: getDate(),
        validate: (val, vals) => {
            console.log("nextDueDate values", val);
            if(vals.totalReceivedAmount === vals.price){
                return null;
            }
            if (new Date(val).getTime() <= new Date(getDate()).getTime()) {
                return "Must be a future date";
            }
            return null;
        }
    }
};

class CreateProperty extends PureComponent {
    render() {
        const { id } = this.props.match.params;
        return (
            <Form
                {...this.props}
                query={PROPERTY}
                defaults={defaults}
                skipOnUpdate={["price", "totalReceivedAmount"]}
            >
                {({ update, defaults, queryResult }) => {
                    // defaults.manager.current = queryResult && queryResult.site.manager.id;
                    console.log(defaults);
                    return (
                        <FormGenerator
                            {...this.props}
                            defaults={defaults}
                            mutation={update ? UPDATE_PROPERTY : CREATE_PROPERTY}
                            title={!update ? "Create a new property" : "Update this property"}
                            submitText={!update ? "Create Property" : "Update Property"}
                            mutateFunc={(fn, data) => { return fn({ variables: { id, data } }); }}
                        >
                        </FormGenerator>
                    );
                }}
            </Form>
            // <Query query={USERS_USERNAME}>
            //     {({ loading, error, data }) => {
            //         if (loading) {
            //             return (<span>loading...</span>);
            //         }
            //         else if (error) {
            //             return (<span>Error Occured!</span>);
            //         }}}</Query>
            // <FormGenerator defaults={defaults} submitText="Create Property" mutation={CREATEPROPERTY} title="Create a new property" {...this.props}></FormGenerator>
        )
    }
};

export default CreateProperty;



